@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
::-webkit-scrollbar {
  display: none;
}
.ant-layout-header,
.ant-layout {
  background-color: white !important;
}
.logo2 {
  width: 110px;
  height: 110px;
  background: url("http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/logo2.png") no-repeat center;
  background-size: contain;
}
.footerColumn {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
}
.itemCenter {
  align-items: center;
}
.textAlignLeft {
  align-items: flex-start;
}
.wetChartIcon {
  width: 71px;
  height: 71px;
  background-color: antiquewhite;
}
.VDivider {
  background-color: black;
  height: 101px;
  width: 1px;
}
.projectIcon {
  width: 35px;
  height: 35px;
  background-color: antiquewhite;
}
.proIcon {
  width: 50px;
  height: 50px;
  background-color: antiquewhite;
}
.proName {
  font-size: 20px;
  font-family: "NotoSansHans-Bold";
}
.customDivider {
  background-color: #DEDEDE;
  margin: 0px !important;
  height: 2px;
}
